nav {
  min-height: 10vh;
  display: flex;
  justify-content: space-around;
  align-items: center;

  .library-toggle {
    background: transparent;
    border: 2px solid rgb(65, 65, 65);
    padding: 0.5rem;
    cursor: pointer;
    transition: 0.3s all ease;
    &:hover {
      background: rgb(65, 65, 65);
      color: white;
    }
  }
}
